html {
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Quicksand", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }

.container {
  width: 1140px;
  margin: 0 auto; }

.page-header__container {
